import { call, put, takeEvery } from 'redux-saga/effects'
import { getMsgList } from '@/api/user'
import { SEND_MSG_LIST } from '@/config/actionTypes'

function* sendMsgList() {
  console.log('sendMsgList')
  try {
    const res = yield call(getMsgList)
    if (res.data.code === 200) {
      yield put({ type: SEND_MSG_LIST, payload: res.data.data })
    }
  } catch (err) {
    console.log(' %c  err123: ', 'font-size:20px;background-color: #42b983;color:#fff;', err)
  }
}

export default function* msgSaga() {
  yield takeEvery('get_msg_list', sendMsgList)
}
