import axios from 'axios'
import store from '../store'
import { baseURL } from '../config'

const CancelToken = axios.CancelToken // axios

class HttpRequest {
  constructor(baseURL) {
    this.baseURL = baseURL
    this.pending = {}
  }

  getInsideConfig() {
    return {
      baseURL: this.baseURL,
      timeout: 60000,
      headers: {
        'Content-Type': 'application/json;charset=utf-8',
      },
    }
  }

  /**
   *, this.pending
    this.removePending(key, true)
   this.pending[key],delete this.pending[key]key
   *config.cancelToken = new CancelToken this.pending[key] = c
   * this.removePending(key, true)this.pending[key]isRequest
   this.pending[key]('')
   * this.removePending(key) isRequestfalse, delete this.pending[key]key
   */

  removePending(key, isRequest = false) {
    if (this.pending[key] && isRequest) {
      this.pending[key](',')
    }
    delete this.pending[key]
  }

  // 
  interceptors(instance) {
    // 
    instance.interceptors.request.use(
      (config) => {
        // console.log('config:' + JSON.stringify(config, null, 2))
        // console.log(config)

        // token,
        let isPublic = false
        const token = store.getState().userState.token
        console.log(' %c  token: ', 'font-size:20px;background-color: #42b983;color:#fff;', token)
        // const token = store.state.token

        // publicPath.map((path) => {
        //   isPublic = isPublic || path.test(config.url) // urltoken
        // })
        if (!isPublic && token) {
          config.headers.Authorization = 'Bearer ' + token
        }

        // 
        const key = config.url + '&' + config.method
        this.removePending(key, true)
        config.cancelToken = new CancelToken((c) => {
          this.pending[key] = c
        })
        return config
      },
      (err) => {
        return Promise.reject(err)
      },
    )
    // 
    instance.interceptors.response.use(
      (response) => {
        const key = response.config.url + '&' + response.config.method
        this.removePending(key)
        return response
      },
      (error) => {
        return Promise.reject(error)
      },
    )
  }

  request(options) {
    const instance = axios.create()
    const newOptions = Object.assign(this.getInsideConfig(), options)
    this.interceptors(instance)
    return instance(newOptions)
  }

  get(url, config) {
    const instance = Object.assign(
      {
        method: 'get',
        url,
      },
      config,
    )
    // const instance = Object.assign({method: 'get', url}, config)
    return this.request(instance)
  }

  post(url, data) {
    return this.request({
      method: 'post',
      url,
      data,
    })
  }
}

const Axios = new HttpRequest(baseURL)
export default Axios
